const jQuery = require('jquery');
const Vue = require('vue');
const _ = require('lodash');
require('jquery.cookie!jquery');    // jQueryプラグインなので、読み込みのみ
const Clipboard = require('clipboard');
const hashChange = require('hashchange');
const Materialize = require('materialize-css');
const constants = require('./constants');
const stringUtil = require('./utility/stringUtil');
const parmalink = require('./utility/parmalink');

(function ($) {
    $(function () {
        let initialValue = {
            text: "吾輩は猫である。名前はまだ無い。\nどこで生れたかとんと見当がつかぬ。何でも薄暗いじめじめした所でニャーニャー泣いていた事だけは記憶している。吾輩はここで始めて人間というものを見た。", // 対象のテキスト
            words: [],    // 単語単位で分割された文章
            tokens: [], // kuromojiで解析した結果
            isAnalyzing: false, // 解析中
            previewType: constants.previewType.preview,    // プレビュー or コード
            // プレビューの設定
            preview: {
                fontSize: 16,   // フォントサイズ
                screenWidth: 200, // プレビューの幅
                showGroupGuide: true    // プレビューに区切りのガイドを表示するか
            },
            // コピー用コードの設定
            option: {
                doNewline: true,    // 改行する
                doDelimiter: false, // 区切る
                delimiterMark: ',', // 区切る文字
                doFormatString: true,   // グループごとの文字列
                beforeString: '<span class="wordwrap" role="presentation">',    // 前に追加する文字
                afterString: '</span>', // 後ろに追加する文字
                doReplaceNewLine: true, // 改行を置換する
                replaceNewlineMark: '<br>', // 改行を置換する文字
                doEndSpaceToEntity: true    // グループの末尾の半角スペースをエンティティに変換する
            }
        };  // デフォルト値
        const analyzer = new Worker('./assets/js/index/worker.js');   // 文章を解析するWorker（kuromoji.jsが重いため）
        const savedPreview = $.cookie(constants.cookieKey.preview);   // 保存したプレビュー設定
        const savedOption = $.cookie(constants.cookieKey.option); // 保存した出力設定

        // 出力設定のプリセット
        const outputFormatPresets = {
            [constants.outputFormatPreset.html]: {
                doDelimiter: false,
                doFormatString: true,
                beforeString: '<span class="wordwrap" role="presentation">',
                afterString: '</span>',
                doReplaceNewLine: true,
                replaceNewlineMark: '<br>',
                doEndSpaceToEntity: true
            },
            [constants.outputFormatPreset.slim]: {
                doNewline: true,
                doDelimiter: false,
                doFormatString: true,
                beforeString: 'span.wordwrap role="presentation" ',
                afterString: '',
                doReplaceNewLine: true,
                replaceNewlineMark: 'br',
                doEndSpaceToEntity: true
            },
            [constants.outputFormatPreset.pug]: {
                doNewline: true,
                doDelimiter: false,
                doFormatString: true,
                beforeString: 'span.wordwrap(role="presentation") ',
                afterString: '',
                doReplaceNewLine: true,
                replaceNewlineMark: 'br',
                doEndSpaceToEntity: true
            },
            [constants.outputFormatPreset.arrayDoubleQuotation]: {
                doNewline: false,
                doDelimiter: true,
                delimiterMark: ', ',
                doFormatString: true,
                beforeString: '"',
                afterString: '"',
                doReplaceNewLine: false,
                doEndSpaceToEntity: false
            },
            [constants.outputFormatPreset.arraySingleQuotation]: {
                doNewline: false,
                doDelimiter: true,
                delimiterMark: ', ',
                doFormatString: true,
                beforeString: '\'',
                afterString: '\'',
                doReplaceNewLine: false,
                doEndSpaceToEntity: false
            },
            [constants.outputFormatPreset.yamlArray]: {
                doNewline: true,
                doDelimiter: false,
                doFormatString: true,
                beforeString: '- "',
                afterString: '"',
                doReplaceNewLine: false,
                doEndSpaceToEntity: false
            }
        };


        //----------------------------------
        //  初期値をハッシュやcookieから復元する
        //----------------------------------

        // ハッシュからテキストを生成する（パーマリンク）
        if (parmalink.hasText()) {
            initialValue.text = parmalink.getText();
        }

        if (savedPreview) {
            const obj = JSON.parse(savedPreview);    // cookieのjsonを復元

            Object.keys(obj).forEach(function(key) {
                if (key in initialValue.preview) {
                    // keyが存在していたら値を反映する
                    const val = this[key];
                    initialValue.preview[key] = val;
                }
            }, obj);

            // 保存期間を延長する
            $.cookie(constants.cookieKey.preview, savedPreview, {
                expires: 30
            });
        }

        // 出力設定をcookieから取得
        if (savedOption) {
            const obj = JSON.parse(savedOption);    // cookieのjsonを復元

            Object.keys(obj).forEach(function(key) {
                if (key in initialValue.option) {
                    // keyが存在していたら値を反映する
                    const val = this[key];
                    initialValue.option[key] = val;
                }
            }, obj);

            // 保存期間を延長する
            $.cookie(constants.cookieKey.option, savedOption, {
                expires: 30
            });
        }


        //----------------------------------
        //  App
        //----------------------------------

        const app = new Vue({
            el: '#app',
            data: initialValue,

            created () {
                this.$nextTick(function () {
                    // ビュー全体が再レンダリングされた後にのみ実行されるコード
                    // 一番最初に、テキストエリアのサイズを調整しておく。initialValue.textが長かったとき用
                    this.fitTextareaSize();

                    // 最初に解析する
                    if (this.text.length > 0) {
                        this.analyzeText();
                    }
                })
            },

            methods: {

                /**
                 * 入力されたテキストをクリア
                 *
                 */
                clearText () {
                    this.text = '';
                },

                /**
                 * 入力テキストを更新する
                 *
                 * @param text
                 */
                updateText (text) {
                    this.text = text;
                },

                /**
                 * 文章を解析する
                 *
                 */
                analyzeText () {
                    // workerで文章を解析する
                    // モバイル、タブレットの場合、許可を取得する
                    analyzer.postMessage(this.text);
                    this.isAnalyzing = true;
                },

                /**
                 * 文章を分割した
                 *
                 * @param words {Array} 単語の配列
                 */
                onAnalyzed (words) {
                    this.words = words;
                    this.isAnalyzing = false;
                },

                /**
                 * プレビューのサイズを設定する
                 *
                 * @param width 幅
                 */
                setPreviewScreenSize (width) {
                    this.preview.screenWidth = width;
                },

                /**
                 * プレビューエリアの文字サイズを設定する
                 *
                 * @param size 文字サイズ
                 */
                setPreviewFontSize (size) {
                    this.preview.fontSize = size;
                },

                /**
                 * 出力設定の追加文字列を指定
                 *
                 * @param before {String} 前の文字
                 * @param after {String} 後ろの文字
                 */
                setOutputFormatString (before, after) {
                    this.option.beforeString = before;
                    this.option.afterString = after;
                },

                /**
                 * プリセットのフォーマットを設定する
                 *
                 * @param formatId {number} フォーマットID
                 */
                setOutputFormat (formatId) {
                    const obj = outputFormatPresets[formatId];

                    Object.keys(obj).forEach(function(key) {
                        if (key in app.option) {
                            // keyが存在していたら値を反映する
                            const val = this[key];
                            app.option[key] = val;
                        }
                    }, obj);
                },

                /**
                 * テキストエリアのサイズを入力内容に併せてリサイズする
                 *
                 */
                fitTextareaSize () {
                    $('#input-textarea, #output-textarea').trigger('autoresize');
                },

                /**
                 * 入力のテキストエリアのサイズを入力内容に併せてリサイズする
                 *
                 */
                fitInputTextareaSize () {
                    $('#input-textarea').trigger('autoresize');
                    $('label[for=input-textarea]').addClass('active');
                },

                /**
                 * 結果のテキストエリアのサイズを入力内容に併せてリサイズする
                 *
                 */
                fitOutputTextareaSize () {
                    $('#output-textarea').trigger('autoresize');
                    $('label[for=output-textarea]').addClass('active');
                }
            },

            computed: {

                /**
                 * プレビュー用の単語リスト
                 *
                 */
                previewItems () {
                    let result = [];
                    this.words.forEach((word) => {
                        const containsNewline = stringUtil.containsNewline(word);

                        // if (endSpaces) {
                        //     // 末尾のスペースを&nbsp;に変換する
                        //     word = word.replace(/ +$/, '') + "&nbsp;".repeat(endSpaces[0].match(/ /g).length);
                        // }

                        if (!containsNewline) {
                            // 改行以外をタグで囲う
                            result.push({
                                word,
                                isNewline: false
                            });
                        }
                        else {
                            // 改行を<br>に変換する
                            word.match(/\r?\n/g).map(() => {
                                result.push({
                                    isNewline: true
                                });
                            });
                        }
                    });

                    return result;
                },

                /**
                 * コピーする用のテキスト
                 *
                 * @return {String} 文字列
                 */
                resultCode () {
                    // 設定を反映する
                    // ※\r?\nを変換する場合は、前後の文字列は追加しないが、カンマ区切り、改行は反映する

                    let words = this.words;
                    words = words.map((word) => {
                        const endSpaces = word.match(/ +$/); // 末尾のスペースを取得
                        const containsNewline = stringUtil.containsNewline(word);

                        if (endSpaces && this.option.doEndSpaceToEntity) {
                            // 末尾のスペースを変換する
                            word = word.replace(/ +$/, '') + "&nbsp;".repeat(endSpaces[0].match(/ /g).length);
                        }

                        if (this.option.doFormatString) {
                            // 前後の文字を追加する。
                            if (!containsNewline) {
                                // 改行以外
                                word = `${this.option.beforeString}${word}${this.option.afterString}`;
                            }
                        }

                        return word;
                    });


                    let delimiter = '';  // 区切り文字
                    if (this.option.doDelimiter) {
                        delimiter += this.option.delimiterMark;
                    }
                    if (this.option.doNewline) {
                        delimiter += '\n';
                    }

                    // 区切り文字で結合する
                    const num = words.length;
                    let result = '';
                    words.forEach((word, index) => {
                        const containsNewline = stringUtil.containsNewline(word);

                        if (!(containsNewline || index === num - 1)) {
                            // 改行 or 最後の文字でなければ区切り文字を追加する
                            result += word + delimiter;
                        }
                        else if (containsNewline) {
                            // 改行
                            if (this.option.doReplaceNewLine) {
                                // 変換する
                                // 複数の改行が一つのトークンとして解析させるので、改行文字ごとに置換する
                                result += word.replace(/\r?\n/g, this.option.replaceNewlineMark + delimiter);
                            }
                            // else if (this.option.doNewline) {
                            //     // この処理は見た目は正しそうだけど、データ的におかしい？ので不要かも
                            //     let newlines = word.match(/\r?\n/g);
                            //     if (newlines) {
                            //         // 単語ごとの改行ありの場合は、本文の改行と重複してしまうので1つ減らす
                            //         newlines.forEach((val, index) => {
                            //             if (index !== 0) {
                            //                 result += val;
                            //             }
                            //         });
                            //     }
                            // }
                            else {
                                // 単語ごとの改行なしの場合は、改行をそのまま使用する
                                result += word;
                            }
                        }
                        else {
                            // 最後の文字
                            result += word;
                        }

                    });

                    return result;
                }
            },

            watch: {
                text() {
                    // テキストが変更されたら解析する
                    this.analyzeText();

                    // パーマリンクに設定する
                    parmalink.setText(this.text);

                    // テキストが変更されたら、処理を待ってテキストエリアをサイズ調整する
                    setTimeout(() => {
                        this.fitInputTextareaSize();
                    }, 0);
                },

                resultCode() {
                    // 結果が変更されたら、処理を待ってテキストエリアをサイズ調整する
                    setTimeout(() => {
                        this.fitOutputTextareaSize();
                    }, 0);
                },

                ['option.beforeString']() {
                    // ラベルのアクティブを制御する
                    if (this.option.beforeString) {
                        $('label[for=option-add-string-before]').addClass('active');
                    }
                    else {
                        $('label[for=option-add-string-before]').removeClass('active');
                    }
                },

                ['option.afterString']() {
                    // ラベルのアクティブを制御する
                    if (this.option.afterString) {
                        $('label[for=option-add-string-after]').addClass('active');
                    }
                    else {
                        $('label[for=option-add-string-after]').removeClass('active');
                    }
                }
            }
        });



        // プレビューの設定が変更されたら
        app.$watch('preview', function () {
            // cookieに保存する
            $.cookie(constants.cookieKey.preview, JSON.stringify(app.preview), {
                expires: 30
            });
        }, {
            deep: true  // オブジェクトを検知する
        });

        // オプションの設定が変更されたら
        app.$watch('option', function () {
            // cookieに保存する
            $.cookie(constants.cookieKey.option, JSON.stringify(app.option), {
                expires: 30
            });
        }, {
            deep: true  // オブジェクトを検知する
        });


        //----------------------------------
        //  WebWorker
        //----------------------------------

        /**
         * 解析するワーカーからの応答
         *
         */
        analyzer.addEventListener('message', function (e) {
            app.onAnalyzed(e.data);
        }, false);



        //----------------------------------
        //  UIの設定
        //----------------------------------

        // Materializeのコンポーネント設定

        // ドロップダウン
        $('.dropdown-button').dropdown({
            inDuration: 300,
            outDuration: 225,
            constrainWidth: false, // Does not change width of dropdown to that of the activator
            hover: false, // Activate on hover
            gutter: 0, // Spacing from edge
            belowOrigin: false, // Displays dropdown below the button
            alignment: 'left', // Displays dropdown with edge aligned to the left of button
            stopPropagation: false // Stops event propagation
        });

        // タブ
        $('ul.tabs').tabs({
            onShow: function (tab) {
                const $tab = $(tab);

                switch ($tab.attr('id')) {
                    case 'output-code':
                        app.fitOutputTextareaSize();
                        break;
                }
            }
        });

        // TODO タブが表示されたときに更新する


        // アコーディオン
        $('.collapsible').collapsible({
            accordion: false    // A setting that changes the collapsible behavior to expandable instead of the default accordion style
        });

        // セレクト
        $('select').material_select();


        // // プレビューサイズのプルダウンが選択されたら変数に設定する
        // $('a[data-preview-width]').on('click', function (e) {
        //     e.preventDefault();
        //     const $button = $(this);
        //     app.setPreviewSize($button.attr('data-preview-width'), $button.attr('data-preview-height'));
        // });

        // // 文字列追加のプルダウンが選択されたら変数に設定する
        // $('a[data-string-before]').on('click', function (e) {
        //     e.preventDefault();
        //     const $button = $(this);
        //     app.setAddString($button.attr('data-string-before'), $button.attr('data-string-after'));
        // });

        // 結果をクリップボードにコピー
        const resultCopy = new Clipboard('#clipboard-copy', {});
        resultCopy.on('success', function (e) {
            Materialize.toast('Copied!', 3000);
        });


        // ハッシュが変更されたら、テキストに反映する
        hashChange.update(function (hashFragment) {
            if (parmalink.hasText()) {
                app.updateText(parmalink.getText());
            }
        });

        // リサイズしたらテキストエリアのサイズを調整する
        $(window).on('resize', _.throttle(function () {
            app.fitTextareaSize();
        }, 500, {
            trailing: true,
            leading: false
        }));


    });
}(jQuery));



